.App {
  font-family: "Rubik", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App .search-ip {
  background-image: url("./images/pattern-bg.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.App .search-ip h1 {
  color: rgb(240, 240, 240);
  font-weight: 300;
}

.App .search-ip form {
  display: flex;
  align-items: center;
}

.App .search-ip form input {
  height: 40px;
  width: 350px;
  padding: 10px 25px;
  border-top-left-radius: 1em;
  border-bottom-left-radius: 1em;
  color: hsl(0, 0%, 17%);
  font-size: 18px;
  border: none;
}

.App .search-ip form button {
  background-color: #000;
  width: 60px;
  height: 60px;
  border: 0;
  border-top-right-radius: 1em;
  border-bottom-right-radius: 1em;
}

.App .search-ip form input::placeholder {
  font-weight: 500;
  color: hsl(0, 0%, 59%);
}

.App .search-ip form input:hover {
  cursor: pointer;
}

.App .search-ip form button {
  margin-left: 0;
}

.App .search-ip form button:hover {
  cursor: pointer;
  background-color: rgb(43, 43, 43);
}

.App .search-ip .show-tracker-data {
  width: 75%;
  position: relative;
  background-color: #fff;
  padding: 1% 0;
  margin-top: -37px;
  border-radius: 1em;
  top: 80px;
  z-index: 999;
}

.App .search-ip .show-tracker-data ul {
  padding-left: 0;
  display: flex;
  justify-content: center;
}

.App .search-ip .show-tracker-data ul li {
  list-style-type: none;
  padding-left: 3%;
  padding-right: 8%;
  border-right: 1px solid hsl(0, 0%, 59%);
}

.App .search-ip .show-tracker-data ul li:last-child {
  border-right: none;
}

.App .search-ip .show-tracker-data ul li .data-key {
  color: hsl(0, 0%, 59%);
  letter-spacing: 1px;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 11px;
}

.App .search-ip .show-tracker-data ul li .data-value {
  color: #000;
  font-size: 22px;
  font-weight: 700;
}

.leaflet-container {
  height: 70vh;
}

.attribution {
  font-size: 11px;
  text-align: center;
  padding-top: 10px;
}
.attribution a {
  color: hsl(228, 45%, 44%);
}

@media only screen and (min-width: 817px) and (max-width: 1000px) {
  .App .search-ip .show-tracker-data {
    width: 90%;
  }
}

@media only screen and (min-width: 769px) and (max-width: 816px) {
  .App .search-ip .show-tracker-data ul li .data-key {
    font-size: 12px;
  }

  .App .search-ip .show-tracker-data ul li .data-value {
    font-size: 16px;
  }
}

@media only screen and (min-width: 426px) and (max-width: 768px) {
  .App .search-ip .show-tracker-data ul li .data-key {
    font-size: 10px;
  }

  .App .search-ip .show-tracker-data ul li .data-value {
    font-size: 14px;
  }
}

@media only screen and (max-width: 425px) {
  .App .search-ip {
    padding-bottom: 130px;
  }

  .App .search-ip form {
    width: 84%;
  }

  .App .search-ip form input {
    height: 29px;
    width: 300px;
    font-size: 14px;
  }

  .App .search-ip form button {
    height: 50px;
  }

  .App .search-ip .show-tracker-data {
    position: absolute;
    width: 84%;
    top: 185px;
    padding: 0;
  }

  .App .search-ip .show-tracker-data ul {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .App .search-ip .show-tracker-data ul li {
    border-right: none;
  }

  .App .search-ip .show-tracker-data ul li .data-key {
    margin-top: 0;
    font-size: 8px;
  }

  .App .search-ip .show-tracker-data ul li .data-value {
    margin-top: 0;
    font-size: 14px;
  }

  .leaflet-container {
    height: 100vh;
  }
}
